import React from 'react';

import './card.css';


const MONTH_LIST = [
    'January', 'February', 'March', 'April',
    'May', 'June', 'July', 'August',
    'September', 'October', 'November', 'December'
];


class BlogEntry extends React.PureComponent {
    state = {data: null};

    renderHeader() {
        const {destination} = this.props;

        const {data} = this.state;
        const [year, month, day] = data.date;

        const dateString = `${MONTH_LIST[month-1]} ${day}, ${year}`;

        return (
            <a href={destination} className='blog-header'>
                <h2 className='blog-title'>{data.title}</h2>
                <p className='blog-date'>{dateString}</p>
            </a>
        );
    }

    renderTag(tag) {
        return <div className='blog-tag'
            key={tag}
            onClick={e => this.props.handleTagClick(e, tag)}
        >{tag}</div>;
    }

    render() {
        const {id, promise, destination} = this.props;
        if (this.state.data === null) {
            promise.then(
                data => this.setState({data: data.metadata, intro: data.default})
            ).catch(
                error => console.error(`Blog post ${id} failed to load:`, error)
            );
            return <div>Loading...</div>;
        }
        const {data, intro} = this.state;
        return (
            <div className='blog-card'>
                {this.renderHeader()}
                <div className='blog-tags'>
                    {data.tags.map(this.renderTag.bind(this))}
                </div>
                <a href={destination} className='blog-info'>
                    {intro()}
                    <div className='blog-info-cover'/>
                </a>
            </div>
        );
    }
}

export default BlogEntry;
